import { atom } from "recoil";

export interface UserInput {
  dDayName: string;
  date: string;
}

/**
 * dDayListState    type    
 * @types UserInput
 * @default { dDayName: "", date: "" }
 */
export const dDayUserInputState = atom<UserInput>({
  key: "dDayUserInputState",
  default: { dDayName: "", date: "" },
});

/**
 * DDayList    DDayInput     .
 * @types UserInput[]
 * @default [{ dDayName: " ", date: "2023-06-30"}]
 */
export const dDayListState = atom<UserInput[]>({
  key: "dDayListState",
  default: [{ dDayName: " ", date: "2023-06-30" }],
});

/**
 *     (dDayListState 5  /  dDayName   )
 * @types boolean
 * @default false
 */
export const isErrorState = atom<boolean>({
  key: "isErrorState",
  default: false,
});
