interface DateInputType extends React.ComponentProps<"input"> {
  w?: number;
  textSize?: "xs";
}
/**
 * D-Day List page -> DDayInput Componenet   Input    Component .
 * @param w number | undefined // input width 
 * @param textSize "xs" | undefiend // placeholder, value   
 * @param rest React.ComponentProps<"input"> // input tag props  
 * @returns Date   input Componenet
 */
const DateInput = ({ w, textSize, className, ...rest }: DateInputType) => {
  return (
    <input
      className={`w-${w} text-${textSize} rounded-md shadow-md hover:animate-pulse ${className}`}
      {...rest}
    />
  );
};

export default DateInput;
