import React, { useState } from "react";
import { useSetRecoilState } from "recoil";
import {
  itemTextBgColor,
  itemTextBgColorType,
  itemTextColor,
  itemTextColorType,
  itemTextSize,
  itemTextSizeType,
} from "../../recoil/diary";
import Button from "./Button";
import Select from "./Select";
import SetSvg from "./SetSvg";
import Text from "./Text";

interface ISetBtn {
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
  isHover?: boolean;
  others?: string;
  isSettingBox?: boolean;
}
/**
 * Main Page, Diary Editor , onClick    , editor content style   .
 * @param onClick React.MouseEventHandler<HTMLButtonElement> // onClick Event  
 * @param isHover boolean | undefined // hover CSS 
 * @param others string | undefined // className  CSS   
 * @param isSettingBox boolean | undefiend // Editor ,     .
 * @returns Setting Button Svg  Component
 */
const SetBtn = ({ onClick, isHover, others, isSettingBox }: ISetBtn) => {
  const [hover, setHover] = useState(false);
  const setTextSize = useSetRecoilState<itemTextSizeType>(itemTextSize);
  const setTextColor = useSetRecoilState<itemTextColorType>(itemTextColor);
  const setTextBgColor =
    useSetRecoilState<itemTextBgColorType>(itemTextBgColor);
  const handleTextSize = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const value: itemTextSizeType = event.currentTarget.value;
    setTextSize(value);
  };
  const handleTextColor = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const value: itemTextColorType = event.currentTarget.value;
    setTextColor(value);
  };
  const handleTextBgColor = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const value: itemTextBgColorType = event.currentTarget.value;
    setTextBgColor(value);
  };
  return (
    <>
      <button
        className={`inline-flex items-center p-2 text-sm rounded-lg focus:outline-none ${
          isHover && `hover:bg-gray-100`
        } text-gray-500 ${others}`}
        onMouseUp={() => setHover(true)}
        onClick={onClick}
      >
        <SetSvg w={16} h={16} />
      </button>
      {isSettingBox && hover && (
        <div className="flex flex-col justify-between p-4 rounded-md w-48 h-72 bg-cyan-200 absolute translate-y-[45%] z-[999]">
          <div>
            <Text gray size="sm">
              Text Size
            </Text>
            <Select onSelectChange={handleTextSize}>
              <option value="text-base">  </option>
              <option value="text-sm">  </option>
              <option value="text-lg">  </option>
            </Select>
          </div>
          <div>
            <Text gray size="sm">
              Text Color
            </Text>
            <Select onSelectChange={handleTextColor}>
              <option value="">  </option>
              <option value="text-green-500">  </option>
              <option value="text-blue-500">  </option>
              <option value="text-red-500">  </option>
            </Select>
          </div>

          <div>
            <Text gray size="sm">
              Highlight Text
            </Text>
            <Select onSelectChange={handleTextBgColor}>
              <option value="">  </option>
              <option value="bg-[yellow]">  </option>
              <option value="bg-[blue]">  </option>
              <option value="bg-[green]">  </option>
              <option value="bg-[pink]">  </option>
            </Select>
          </div>

          <div className="flex justify-end gap-2">
            <Button
              white
              hover
              bgColor="red"
              className="w-14 h-8"
              onClick={() => setHover(false)}
            >
              
            </Button>
            <Button
              white
              hover
              bgColor="green"
              className="w-14 h-8"
              onClick={onClick}
            >
              
            </Button>
          </div>
        </div>
      )}
    </>
  );
};

export default SetBtn;
