import { RefObject, useEffect, useRef, useState } from "react";
import { useRecoilState, useRecoilValue, useSetRecoilState } from "recoil";
import {
  apply,
  bgImage,
  resultTemplate,
  strokeColor,
  strokeWidth,
} from "../../../recoil/diary";

interface ITemplate {
  width: number;
  height: number;
}
/**
 * template  componenet,     
 * @param width  Ref.current.offsetWidth
 * @param height  Ref.current.offsetWidth
 * @returns DayDiary     ,  
 */
const Template = ({ width, height }: ITemplate) => {
  const canvasRef: RefObject<HTMLCanvasElement> =
    useRef<HTMLCanvasElement>(null);
  const [context, setContext] = useState<CanvasRenderingContext2D>();
  const templateImg = useRecoilState<string>(bgImage);
  const [isDrawing, SetIsDrawing] = useState<boolean>(false);
  const stroke = useRecoilValue<number>(strokeWidth);
  const color = useRecoilValue<string>(strokeColor);

  const setResultTemplate = useSetRecoilState<string | undefined>(
    resultTemplate
  );
  const isApply = useRecoilValue<boolean>(apply);

  // stroke style  (stroke    )
  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas?.getContext("2d");
    if (ctx) {
      ctx.strokeStyle = color;
      ctx.lineWidth = stroke;
      setContext(ctx);
    }
  }, [stroke, color]);
  // background img  (width, height,     )
  useEffect(() => {
    const img = new Image();
    img.src = templateImg[0];
    img.onload = () => {
      context?.drawImage(img, 0, 0, width, height);
    };
  }, [templateImg[0], width, height]);

  useEffect(() => {
    setResultTemplate(canvasRef.current?.toDataURL());
  }, [isApply]);
  const drawing = (event: React.MouseEvent<HTMLCanvasElement, MouseEvent>) => {
    const { offsetX, offsetY } = event.nativeEvent;
    if (!isDrawing) {
      context?.beginPath();
      context?.moveTo(offsetX, offsetY);
    } else {
      context?.lineTo(offsetX, offsetY);
      context?.stroke();
    }
  };

  return (
    <canvas
      ref={canvasRef}
      width={width}
      height={height}
      onMouseMove={drawing}
      onMouseDown={() => SetIsDrawing(true)}
      onMouseUp={() => SetIsDrawing(false)}
      onMouseLeave={() => SetIsDrawing(false)}
    />
  );
};

export default Template;
