interface ButtonType extends React.ComponentProps<"button"> {
  white?: boolean;
  bgColor: "red" | "green" | "sky" | "emerald";
  weight?: "medium";
  hover?: boolean;
}
type buttonBgColorType = {
  [key: string]: string;
};
const buttonBgColor: buttonBgColorType = {
  red: "bg-red-500",
  green: "bg-green-500",
  sky: "bg-sky-500",
  emerald: "bg-emerald-500",
};
/**
 * rounded & shadow medium default + hover ->       button
 * @param w number | full | undefined // pixel or full
 * @param h number | undefined // pixel
 * @param white // boolean | undefined // text color  (white)
 * @param bgColor // "red" | "green" | "sky" | "emerald" | "green" | undefined //   
 * @param weight // "medium" // font-weight 
 * @param hover // boolean // hover   
 * @param rest // extends React.ComponentProps<"button>"  button tag props  
 * @returns Button Componenet rounded & shadow default
 */
const Button = ({
  white,
  bgColor,
  weight,
  hover,
  className,
  ...rest
}: ButtonType) => {
  return (
    <button
      className={`${buttonBgColor[bgColor]} ${
        white && "text-white"
      } font-[${weight}] ${
        hover && `hover:${buttonBgColor[bgColor]}`
      } font-mono rounded-md shadow-md ${className}`}
      {...rest}
    ></button>
  );
};

export default Button;
