import React from "react";

interface ContainerType extends React.ComponentProps<"div"> {
  children: React.ReactNode;
  direction?: "row" | "col";
  justifyContent?: "center" | "between" | "end";
  alignItems?: "center" | "end";
  mdGrid?: boolean;
}
/**
 *  Flex Container
 * @param children React.ReactNode
 * @param direction "row" | "col" | undefined //  
 * @param justifyContent "center" | "between" | undefined // main Axis 
 * @param alignItems "center" | undefined // cross Axis 
 * @param rest React.ComponentProps<"div"> // div extends  div tag props  
 * @param mdGrid boolean | undefined // md   display : grid, repeat(1fr 1fr) .
 * @returns  Flex Container  
 */
const Container = ({
  children,
  direction = "row",
  justifyContent,
  alignItems,
  className,
  mdGrid,
  ...rest
}: ContainerType) => {
  return (
    <div
      className={`flex flex-${direction} justify-${justifyContent} items-${alignItems} ${
        mdGrid && `md:grid md:grid-cols-2`
      } ${className}`}
      {...rest}
    >
      {children}
    </div>
  );
};
export default Container;
