interface BgImgType extends React.ComponentProps<"img"> {
  src?: string;
  absolute?: boolean;
  z?: boolean;
}
/**
 * alt img   img component    component
 * @param src string | undefined // img src  
 * @param absolute boolean | undefined // img position(absolute)  
 * @param z boolean | undefiend // z-index   (-1)
 * @param rest React.ComponenetProps<"img"> // img tag props  
 * @returns img component (style )
 */
const BgImg = ({ src, absolute, z, className, ...rest }: BgImgType) => {
  return (
    <img
      src={src}
      alt={`${process.env.PUBLIC_URL}/public/noImg.png`}
      className={`${src!.length < 10 && `hidden`} ${absolute && "absolute"} ${
        z && "z-[-1]"
      } overflow-hidden w-[inherit] h-[inherit] rounded-md ${className}`}
      {...rest}
    />
  );
};
export default BgImg;
